/* eslint-disable react/jsx-filename-extension */
import React from 'react';

import Layout from '../components/layouts/MainLayout';
import SEO from '../components/common/Seo';
import Mong from '../components/ui/images/Mong';

const IndexPage = () => (
  <Layout>
    <main className="relative">
      <SEO title="Home" keywords={['John Croker', 'portfolio', 'website']} />

      <div className="fixed bottom-0 right-0 opacity-25 -mb-5" style={{ width: '500px' }}>
        <Mong />
      </div>

      <div className="flex">
        <div className="font-title text-6xl flex items-end" style={{ width: '160px' }}>
          <div className="transform-90-md fixed bottom-0 left-0 screen-h">
            <h1 className="text-pink-900">John Croker</h1>
          </div>
        </div>
        <div className="flex-grow">
          <ul className="projects stroke font-title text-6xl leading-none ">
            <li className="py-4"><a href="https://www.financialservicesexpo.co.uk" className="relative inline-block" data-info="2019" rel="noopener noreferrer" target="_blank">Financial Services Expo</a></li>
            <li className="py-4"><a href="https://www.project-insights.co.uk" className="relative inline-block" data-info="2019" rel="noopener noreferrer" target="_blank">Project Insights</a></li>
            <li className="py-4"><a href="https://www.mortgagesleepout.co.uk" className="relative inline-block" data-info="2018" rel="noopener noreferrer" target="_blank">The Mortgage Sleepout</a></li>
            <li className="py-4"><a href="https://www.frwra.co.uk" className="relative inline-block" data-info="2018" rel="noopener noreferrer" target="_blank">Women&apos;s Recognition Awards</a></li>
            <li className="py-4"><a href="https://www.financialreporter.co.uk" className="relative inline-block" data-info="2018" rel="noopener noreferrer" target="_blank">Financial Reporter</a></li>
            <li className="py-4"><a href="https://www.specialistlendingroadshow.co.uk" className="relative inline-block" data-info="2018" rel="noopener noreferrer" target="_blank">Specialist Lending Roadshow</a></li>
            <li className="py-4"><a href="https://www.frontevents.co.uk" className="relative inline-block" data-info="2017" rel="noopener noreferrer" target="_blank">Front Events</a></li>
            <li className="py-4"><a href="https://www.fs-cup.co.uk" className="relative inline-block" data-info="2017" rel="noopener noreferrer" target="_blank">Financial Services Cup</a></li>
            <li className="py-4"><a href="https://nma-awards.co.uk" className="relative inline-block" data-info="2017" rel="noopener noreferrer" target="_blank">National Mortage Advisor Awards</a></li>
          </ul>
        </div>
      </div>


    </main>
  </Layout>
);

export default IndexPage;
